import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/createWord',
    name: 'routeName',
    component: () => import('../views/CreateWord.vue'),
  },
  {
    path: '/createExcel',
    name: 'createExcel',
    component: () => import('../views/CreateExcel.vue'),
  },
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
